
///////////////////  Outro  ///////////////////

#outro {
  @extend %article-wrapper;
  height: 100vh;
  display: flex;
  align-items: center;

  & .section-header {
    position: static;
  }

  & .section-header-img-wrapper {
    margin-bottom: 3rem;
  }

  & .section-header-img {
    position: relative;
    left: 5%;
  }
  & .section-header-nav {
    margin-top: 3rem;
  }

  & .section-header-sub-title {
    @include text-thin-title();
    margin-bottom: 0;
    @include media-breakpoint-up(xl) {
      margin-bottom: 1.8rem;
    }
  }

}